<template>
  <div class="container">

    <div class="row">
      <div class="col-sm-10 col-md-8 col-lg-6 mx-auto Blurb">
        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Morbi diam nisl, vehicula vel nunc maximus, faucibus pellentesque
          nibh. Sed sed pharetra erat, ut laoreet diam. Praesent volutpat egestas nulla in pharetra.</p>
      </div>
    </div>

    <div class="row">
      <div class="col-lg-10 mx-auto">
        <svg class="ie" version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px" viewBox="0 0 2376.8 1394.1"
          style="enable-background:new 0 0 2376.8 1394.1;" xml:space="preserve">
          <g id="Layer_1">

            <image style="overflow:visible;enable-background:new    ;" width="1110" height="651" xlink:href="./../../../static/images/wolf-explore.jpg" transform="matrix(2.1413 0 0 2.1413 -2.594803e-02 4.424129e-02)">
            </image>
          </g>
          <g id="Layer_2">
            <rect x="953" y="968" class="st0" fill-opacity="0.25" width="656" height="426.1" @click="updateState('wolf-pup')" />
          </g>
        </svg>
        <img src="http://via.placeholder.com/200x100">
      </div>
    </div>

  </div>
</template>

<script>
const _ = require('lodash')
import content from './../../../static/data/content.json'
export default {
  data () {
    return {
      content
    }
  },

  methods: {
    // changeAnimation (animation) {
    //   let payload = {
    //     parent: 'wolfmap',
    //     key: 'detailAnimation',
    //     value: animation
    //   }
    //   this.$store.dispatch('setProp', payload)
    // },

    // changeDetail (detailObj) {
    //   let payload = {
    //     parent: 'wolfmap',
    //     key: 'currentDetail',
    //     value: detailObj
    //   }
    //   this.$store.dispatch('setProp', payload)
    // },

    // changeView (view) {
    //   let payload = {
    //     parent: 'wolfmap',
    //     key: 'currentView',
    //     value: view
    //   }
    //   this.$store.dispatch('setProp', payload)
    // },

    // selectDetail (selection) {
    //   let detailObj = this.getDetail(selection)
    //   this.changeDetail(detailObj)
    //   this.changeAnimation('slideUp')
    //   this.changeView('appWolfDetail')
    // },

    updateState (selection) {
      let detailObj = this.getDetail(selection)
      let payload = {
        parent: 'wolfmap',
        updates: {
          currentDetail: detailObj,
          currentView: 'appWolfDetail',
          detailAnimation: 'slideUp'
        }
      }
      this.$store.dispatch('updateProps', payload)
    },

    getDetail (selection) {
      return _.find(content.wolf.mapDetail, { name: selection })
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";

.Blurb {
  background-color: $transparentGray;
  color: $offwhite;
  font-family: $fontSerif;
  line-height: 2em;
  margin: 8em auto 5em auto;
  padding: 2em;

  & p {
    margin: 0;
  }
}
// HACK: required for proper svg sizing in IE
@media screen and (-ms-high-contrast: active),
(-ms-high-contrast: none) {
  .ie {
    margin-top: -5em;
    height: 651px;
  }
}

</style>



