<template>
  <div class="container">

    <div class="row">
      <div class="col" style="margin-top: 8em">
        <div class="ButtonIcon ButtonIcon--small ButtonIcon--light mx-auto" @click="updateState(null, 'appWolfmap', 'slideDown')">
        <!-- <div class="ButtonIcon ButtonIcon--small ButtonIcon--light mx-auto" @click="changeView('appWolfmap')"> -->
          <icon name="arrow-up" scale=".85"></icon>
        </div>
      </div>
    </div>

    <div class="row Detail">

      <div class="col-1 Detail-button">
        <!-- <div class="ButtonIcon ButtonIcon--small ButtonIcon--light" @click="changeDetail(prevDetail)"> -->
        <div class="ButtonIcon ButtonIcon--small ButtonIcon--light" @click="updateState(prevDetail, 'appWolfDetail')">
          <icon name="arrow-left" scale=".85"></icon>
        </div>
      </div>

      
      <div class="col-10">
        <img :src="'./static/images/' + wolfState.currentDetail.image" :alt="wolfState.currentDetail.title">
      </div>

      <div class="col-1 Detail-button">
        <!-- <div class="ButtonIcon ButtonIcon--small ButtonIcon--light" @click="changeDetail(nextDetail)"> -->
        <div class="ButtonIcon ButtonIcon--small ButtonIcon--light" @click="updateState(nextDetail, 'appWolfDetail', 'slideUp')">
          <icon name="arrow-right" scale=".85"></icon>
        </div>
      </div>

    </div>

    <div class="row">
      <div class="col-lg-6 Detail-desc">
        <h2>{{ wolfState.currentDetail.title }}</h2>
        <p>{{ wolfState.currentDetail.description }}</p>
      </div>
    </div>

  </div>
</template>

<script>
const _ = require('lodash')
import content from './../../../static/data/content.json'
import Icon from 'vue-awesome/components/Icon'
export default {
  components: {
    Icon
  },

  data () {
    return {
      content
    }
  },

  computed: {
    nextDetail () {
      let details = this.content.wolf.mapDetail
      let i = this.getDetailIndex(this.wolfState.currentDetail.name)
      if (i === details.length - 1) {
        return this.content.wolf.mapDetail[0]
      } else {
        i += 1
        return this.content.wolf.mapDetail[i]
      }
    },

    prevDetail () {
      let details = this.content.wolf.mapDetail
      let i = this.getDetailIndex(this.wolfState.currentDetail.name)
      if (i === 0) {
        i = details.length - 1
        return this.content.wolf.mapDetail[i]
      } else {
        i -= 1
        return this.content.wolf.mapDetail[i]
      }
    },

    wolfState () {
      return this.$store.getters.wolfState
    }
  },

  methods: {
    // changeDetail (detailObj) {
    //   let payload = {
    //     parent: 'wolfmap',
    //     key: 'currentDetail',
    //     value: detailObj
    //   }
    //   this.$store.dispatch('setProp', payload)
    // },

    // changeView (view) {
    //   let payload = {
    //     parent: 'wolfmap',
    //     key: 'currentView',
    //     value: view
    //   }
    //   this.$store.dispatch('setProp', payload)
    // },

    getDetailIndex (detailName) {
      return _.findIndex(this.content.wolf.mapDetail, { name: detailName })
    },

    updateState (selection, view, animation) {
      let payload = {
        parent: 'wolfmap',
        updates: {
          currentDetail: selection,
          currentView: view,
          detailAnimation: animation || 'fade'
        }
      }
      this.$store.dispatch('updateProps', payload)
    }
  },

  watch: {
    wolfState: state => {
      console.dir('changed' + state)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";

img {
  max-width: 100%;
}

.Detail {
  margin: 2em 0;
}

.Detail-button {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Detail-desc {
  background: $transparentGray;
  color: $offwhite;
  font-family: $fontSerif;
  margin: 0 auto;
  padding: 2em;
}

.fade-in {
  animation: fade-in .3s ease forwards;
}

.fade-out {
  animation: fade-out .3s ease forwards;
}

.slide-left {
  animation: slide-left .3s ease forwards;
}

.slide-right {
  animation: slide-right .3s ease forwards;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-out {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes slide-in {
  0% {
    transform: translateX(-100vw);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes slide-left {
  0% {
    transform: translateX(0);
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 0;
    transform: translateX(100vw);
  }
  75% {
    opacity: .5;
  }
  100% {
    opacity: 1;
    transform: translateX(0)
  }
}

@keyframes slide-right {
  0% {
    transform: translateX(0);
  }
  25% {
    opacity: 0.5;
  }
  50% {
    opacity: 0;
    transform: translateX(-100vw);
  }
  75% {
    opacity: .5;
  }
  100% {
    opacity: 1;
    transform: translateX(0)
  }
}
</style>



